<template>
  <div class="welcome-panel">
    <!--  script server logo -->
    <img 
      :src="imageSrc" 
      alt="script server logo" 
      class="logo-image" 
      @click="handleClick"
    >

    <!--  -->
    <div class="welcome-text">
      Welcome to the Script Server. <br> To start, select one of the scripts.
    </div>
  </div>
</template>

<script>
import ConsoleImage from '@/assets/console.png';
import WechartImage from '@/assets/wechart.jpg';
import { mapActions } from 'vuex';

export default {
  name: 'AppWelcomePanel',
  data() {
    return {
      imageSrc: ConsoleImage,  //  ConsoleImage
      wechartImageSrc: WechartImage,
      timeoutId: null  // setTimeoutID
    }
  },

  methods: {
    ...mapActions('page', ['setLoading']),
    handleClick() {
      this.imageSrc = this.wechartImageSrc; // 
      clearTimeout(this.timeoutId); // 

      // 5 ConsoleImage
      this.timeoutId = setTimeout(() => {
        this.imageSrc = ConsoleImage;
      }, 5000);
    }
  },

  mounted() {
    this.setLoading(false);
  },

  beforeDestroy() {
    // 
    clearTimeout(this.timeoutId);
  }
}
</script>

<style scoped>
.welcome-panel {
  flex: 1;
  color: var(--font-color-medium);
  display: flex;
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  overflow: hidden;
}

.logo-image {
  width: 15ch;  /* 15 */
  height: auto; /*  */
  cursor: pointer; /*  */
}

.welcome-text {
  margin-top: 15px;
}

.welcome-cookie-text {
  margin-top: 8px;
}
</style>
